import toolbar from './toolbar';
import { queries } from '../queries';
import { commands } from '../commands';
import { actions, isActions } from '../actions';
import onAction from '../handlers/onAction';
import onKeyDown from '../handlers/onKeyDown';
import type { MobileBiFactory } from '@ali/4ever-cangjie';
import { key } from '../../utils/meta';

export const mobileBiFactory: MobileBiFactory = (config) => {
  const historyConfig = config.history || {};

  return {
    key,
    toolbar: toolbar(historyConfig),
    queries: {
      canUndo: historyConfig.canUndo ? historyConfig.canUndo : queries.canUndo,
      canRedo: historyConfig.canRedo ? historyConfig.canRedo : queries.canRedo,
    },
    commands,
    actions,
    isActions,
    onAction,
    onKeyDown,
    models: [],
  };
};
